import React from "react";
import { Bar } from "react-chartjs-2";
import { Chart as ChartJS } from "chart.js/auto";

const StockChart = ({ chartData, options }) => {
  options = {
    responsive: true,
    plugins: {
      title: {
        display: true,
        text: "Chart.js Bar Chart - Stacked",
      },
    },
    responsive: true,
    scales: {
      x: {
        stacked: true,
      },
      y: {
          min:0,
        stacked: true,
      },
    },
  };
  return (
    <div
      style={{
        width: "100%",
        backgroundColor: "white",
        boxShadow: "0 0 4px 0 #00000014,0 2px 4px 0 #0000001f",
        borderRadius: "7px",
      }}
    >
      <p
        className="p-3"
        style={{
          margin: "0px",
          color: "#878a99",
          fontWeight: "500",
        }}
      >
        Change in selected period
      </p>
      <Bar data={chartData} options={options} />
    </div>
  );
};

export default StockChart;
