import React from "react";
import { Link } from "react-router-dom";

const HorisontalNav = () => {
  return (
    <>
      <div
        className="row justify-content-between align-items-center"
        style={{
          padding: "15px",
          height: "10vh",
          // position: "absolute",
          width: "100%",
          backgroundColor: "white",
          margin: "0",
        }}
      >
        <div className="col-2">
          <Link className="navLink" to="/" style={{ fontSize: "25px" }}>
            News
            <span style={{ color: "#bb7e38", fontWeight: "bold" }}>
              markets
            </span>
          </Link>
        </div>
        <div className="col-6 col-md-8 d-none d-lg-block">
          <nav
            className="horizontalNav"
            style={{
              textAlign: "center",
              color: "#54575a",
              fontFamily: "cursive",
              fontWeight: "bold",
            }}
          >
            <Link
              style={{
                textAlign: "center",
                color: "#54575a",
                fontFamily: "cursive",
                fontWeight: "bold",
              }}
              to="/dashboard"
            >
              Dashboard
            </Link>
            <Link
              style={{
                textAlign: "center",
                color: "#54575a",
                fontFamily: "cursive",
                fontWeight: "bold",
              }}
              to="/dashboard"
            >
              About
            </Link>
            <Link
              style={{
                textAlign: "center",
                color: "#54575a",
                fontFamily: "cursive",
                fontWeight: "bold",
              }}
              to="#"
            >
              Price
            </Link>
            <Link
              style={{
                textAlign: "center",
                color: "#54575a",
                fontFamily: "cursive",
                fontWeight: "bold",
              }}
              to="#"
            >
              Contacts
            </Link>
          </nav>
        </div>
        <div className="col-6 col-md-2" style={{display:'flex', alignItems:'center' ,justifyContent:'flex-end'}}>
          <Link
            style={{
              textAlign: "center",
              color: "#54575a",
              fontFamily: "cursive",
              fontWeight: "bold",
            }}
            to="#"
          >
            Login
          </Link>
          <Link
            className="btn btn-warning ms-3"
            style={{
              textAlign: "center",
              color: "#54575a",
              fontFamily: "cursive",
              fontWeight: "bold",
              backgroundColor: "#fab131",
            }}
            to="/dashboard"
          >
            Get Started
          </Link>
        </div>
      </div>
    </>
  );
};

export default HorisontalNav;
