import React from "react";

const SingleDataCard = ({ name, data }) => {
  function changeName(name) {
    switch (name) {
      case "c":
        return "Close Price";
        break;
      case "h":
        return "Highest Price";
        break;
      case "l":
        return "Lowest Price";
        break;
      case "n":
        return "Number of Transactions";
        break;
      case "o":
        return "Open Price";
        break;
      case "t":
        return "Time Span";
        break;
      case "v":
        return "Trading Volume";
        break;
      case "vw":
        return "Volume Weighted Average Price";
        break;
      default:
        return "data";
    }
  }
  return (
    <div className="col-12 col-sm-6 col-md-4 col-lg-3 p-3">
      <div
        style={{
          width: "100%",
          backgroundColor: "white",
          boxShadow: "0 0 4px 0 #00000014,0 2px 4px 0 #0000001f",
          borderRadius: "7px",
        }}
      >
        <p
          className="p-3"
          style={{
            margin: "0px",
            color: "#878a99",
            fontWeight: "500",
          }}
        >
          {changeName(name)}
        </p>
        <h6 className="p-3 text-center">{
          (()=>{
            return data ? data :''
          })()
        }</h6>
      </div>
    </div>
  );
};

export default SingleDataCard;
